.footer-container {
  margin-top: 40px;
  margin-bottom: 28px;
  padding: 0 42px;
  display: flex;
  align-items: center;
  text-align: center;
}

.footer__icon {
  margin-left: 7px;
  margin-right: 7px;
  margin-top: 8px;
  animation: heartbeat 1s infinite ease-in-out;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1);
  }
}

.footer-text {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.143;

  color: var(--color-text-header-d);
}

.footer-link {
  color: var(--color-text-header-d);
  text-decoration: underline;
}

@media screen and (max-width: 295px) {
  .footer-container {
    padding: 0 22px;
  }
}
@media screen and (min-width: 296px) and (max-width: 311px) {
  .footer-container {
    padding: 0 30px;
  }
}
@media screen and (min-width: 312px) and (max-width: 319px) {
  .footer-container {
    padding: 0 35px;
  }
}
@media screen and (min-width: 768px) {
  .footer-container {
    padding: 0 20px;
    justify-content: center;
    margin-top: 60px;
    margin-bottom: 40px;
  }
  .footer__icon {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 0;
  }
}
@media screen and (min-width: 1280px) {
  .footer-container {
    margin-top: 100px;
    margin-bottom: 80px;
  }
}
