:root {
  --color-black-header: #000;
  --color-text-header-l: #595959;
  --color-text-header-d: #b7b7b7;
  --color-text-description: #f8f8f8;
  --color-black: #111;
  --color-second-black: #282828;
  --color-white: #fff;
  --color-text-button: #f87719;
  --color-button: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --color-button-hover: linear-gradient( 141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91% );
  --transition-default: 250ms cubic-bezier(.4, 0, .2, 1);
  --shadow: 1px 1px 14px rgba(1, 1, 1, .25);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: inherit;
}

:active, :hover, :focus {
  outline-offset: 0;
  outline: 0;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

ul, li, a, p {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, link {
  cursor: pointer;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (min-width: 320px) {
  .container {
    min-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.button-general, .button-watch-trailer, .button-rem-me, .button-my-libr {
  background: var(--color-button);
  background-color: var(--color-text-button);
  border: 1px solid var(--color-text-button);
  color: var(--color-black);
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  transition-property: background-color, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button-my-libr {
  background: var(--color-black);
  color: var(--color-white);
  border: 1px solid var(--color-text-button);
}

.button-general:hover, .button-watch-trailer:hover, .button-rem-me:hover {
  border: 1px solid var(--color-text-button);
  color: var(--color-text-button);
  background: #fff;
}

.button-my-libr:hover {
  background: var(--color-white);
  color: var(--color-text-button);
  border: none;
}

.button-round-pag, .button-round-search {
  width: 24px;
  height: 24px;
  background: var(--color-button);
  color: #fff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button-round-search {
  width: 40px;
  height: 40px;
  fill: var(--color-white);
}

.button-round-pag:hover, .button-round-search:hover {
  background: var(--color-button-hover);
  color: var(--color-text-header-l);
  fill: var(--color-text-header-l);
}

.button-up {
  opacity: 0;
  visibility: hidden;
  z-index: 8;
  border: 2px solid var(--color-black);
  width: 50px;
  height: 50px;
  text-align: center;
  color: #494848;
  background-image: url("pngwing.af3a53b3.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50px 50px;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-family: Film P3;
  font-size: 64px;
  font-weight: 700;
  transition: background-color .3s, opacity .5s, visibility .5s;
  animation: animatebut 3s 1s infinite;
  display: inline-flex;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.button-up:hover, .button-up:focus {
  animation: animatebut step-start;
}

.arrow-up {
  opacity: 0;
  visibility: hidden;
}

.arrow-up span {
  z-index: 3;
  width: 15px;
  height: 15px;
  border-left: 3px solid var(--color-button);
  border-top: 3px solid var(--color-text-button);
  text-shadow: 1px 1px 2px #494848;
  margin: -2px;
  animation: arrow-up 3s infinite;
  display: block;
  position: fixed;
  bottom: 70px;
  right: 60px;
}

.arrow-up span:nth-child(2) {
  animation-delay: -.1s;
  bottom: 90px;
  right: 70px;
}

.arrow-up span:nth-child(3) {
  animation-delay: -.2s;
  bottom: 75px;
  right: 80px;
}

.button-up.button-up-show, .arrow-up.button-up-show {
  opacity: 1;
  visibility: visible;
}

@keyframes arrow-up {
  0% {
    opacity: 0;
    transform: rotate(45deg)translate(10px, 10px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(45deg)translate(-10px, -10px);
  }
}

@keyframes animatebut {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(90deg);
  }

  100% {
    transform: rotateY(0deg);
  }
}

@media screen and (min-width: 768px) {
  .button-general, .button-watch-trailer, .button-rem-me {
    font-size: 14px;
    line-height: 1.1;
  }

  .button-my-libr {
    font-size: 12px;
    line-height: 1.3;
  }

  .button-round-pag {
    width: 32px;
    height: 32px;
  }

  .button-round-search {
    width: 44px;
    height: 44px;
  }

  .button-up {
    bottom: 50px;
    right: 50px;
  }
}

@media screen and (min-width: 1280px) {
  .button-general {
    font-size: 18px;
    line-height: 1.5;
  }

  .button-watch-trailer {
    font-size: 16px;
    line-height: 1.5;
  }

  .button-round-pag {
    width: 40px;
    height: 40px;
  }

  .button-round-search {
    width: 48px;
    height: 48px;
  }
}

.m-w-t-is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.m-w-t-bakdrop {
  transition-property: opacity scale visibility;
  width: 100%;
  height: 100%;
  background: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  transition-duration: .2s;
  transition-timing-function: linear;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.m-w-t-window {
  max-width: 280px;
  min-height: auto;
  background: var(--color-black);
  border-radius: 16px;
  padding: 24px 24px 8px;
  transition-duration: .2s;
  transition-timing-function: linear;
  position: fixed;
  transform: scale(1);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.m-w-t-bakdrop .m-w-t-is-hidden .m-w-t-window {
  transform: scale(.5);
}

.m-w-t-button-close {
  cursor: pointer;
  fill: var(--color-text-description);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
  margin-left: auto;
  transition-property: background-color, border-radius, fill;
  transition-duration: .2s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.m-w-t-button-close:hover {
  fill: var(--color-text-button);
}

.m-w-t-svg {
  width: 8px;
  height: 8px;
}

.m-w-t-text {
  color: var(--color-white);
  margin-bottom: 24px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.m-w-t-img {
  display: flex;
}

@media screen and (min-width: 768px) {
  .m-w-t-window {
    max-width: 609px;
    padding: 30px 30px 0 0;
  }

  .m-w-t-svg {
    width: 11px;
