body {
  background-color: var(--color-black);
}

.header-wrap {
  background-color: #000;
  position: relative;
}

.header-container {
  max-width: 320px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  overflow: visible;
}

.modal-opened {
  height: 100vh;
  overflow: hidden;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  display: flex;
  position: relative;
}

.header-logo {
  width: 30px;
  height: 24px;
  margin-left: 1px;
  display: block;
}

.logo-image {
  width: 30px;
  height: 24px;
}

.header-menu {
  color: var(--color-text-header-d);
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  transition: color .2s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.nav-wrap {
  width: 157px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.theme-switch-btn {
  margin-left: 5px;
}

@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideOutToLeft {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    opacity: 0;
    transform: translateX(-100%);
  }
}

.mobile-menu-wrap {
  z-index: 2;
  height: 100vh;
  width: 63.75%;
  background: #171717;
  border-radius: 0 8px 8px 0;
  padding: 12px 0 12px 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-menu-wrap.hidden {
  transition: transform .2s cubic-bezier(.4, 0, .2, 1), opacity .2s cubic-bezier(.4, 0, .2, 1);
  animation: slideOutToLeft .2s forwards;
  display: block;
  transform: translateX(-204px);
}

.mobile-menu-wrap:not(.hidden) {
  animation: slideInFromLeft .2s forwards;
}

.mobile-menu-wrap > .header-logo {
  margin-left: 1px;
}

.mobile-menu {
  margin: 44px 0 0;
}

.menu-title {
  display: none;
}

.menu-item {
  color: var(--color-white);
  margin-bottom: 24px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
}

.menu-item:last-child {
  margin-bottom: 0;
}

.menu-item-txt {
  color: var(--color-text-header-d);
}

.mobile-menu-backdrop {
  width: 400vw;
  height: 100vh;
  background: rgba(255, 255, 255, .2);
  position: absolute;
  top: 0;
  left: -100%;
}

.hidden, .header-title, .mobile-menu-title, .desktop-menu {
  display: none;
}

@media screen and (min-width: 768px) {
  .mobile-menu-wrap, .mobile-menu-backdrop {
    display: none;
  }

  .header-container {
    box-sizing: border-box;
    max-width: 768px;
    padding: 12px 32px;
  }

  .header {
    height: 36px;
    justify-content: flex-start;
    padding-top: 0;
    padding-bottom: 0;
  }

  .nav-wrap {
    width: fit-content;
    justify-content: flex-start;
  }

  .header-logo, .logo-image {
    width: 45px;
    height: 36px;
  }

  .header-title {
    color: var(--color-white);
    margin-left: 9.5px;
    margin-right: 59px;
    font-size: 24px;
    font-weight: 400;
    line-height: 1.17;
    display: block;
  }

  .header-menu {
    display: none;
  }

  .desktop-menu {
    gap: 32px;
    margin: 0;
    display: flex;
  }

  .theme-switch-btn {
    margin-left: 169px;
  }

  .menu-item {
    margin-bottom: 0;
