  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-20px);
  }

  60% {
    transform: translateY(-10px);
  }
}

.header-title, .logo-image {
  animation: bounce 2s;
}

.checkbox {
  opacity: 0;
  position: absolute;
}

.checkbox-label {
  box-sizing: border-box;
  background: var(--color-button);
  height: 100%;
  cursor: pointer;
  width: 44px;
  height: 18px;
  border-radius: 194px;
  justify-content: space-between;
  align-items: center;
  padding: 3px;
  display: flex;
  position: relative;
}

.ball-icon {
  background-color: var(--color-white);
  width: 13.2px;
  height: 13.5px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: transform .2s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

.checkbox:checked + .checkbox-label .ball-icon {
  transform: translateX(25px)rotate(180deg);
}

.sun-icon {
  width: 9.26px;
  height: 9px;
}

.moon-icon {
  width: 9.53px;
  height: 9.75px;
}

@media screen and (min-width: 768px) {
  .checkbox-label {
    width: 60px;
    height: 24px;
    padding: 4px;
  }

  .checkbox:checked + .checkbox-label .ball-icon {
    transform: translateX(34px)rotate(180deg);
  }

  .sun-icon, .moon-icon {
    width: 13px;
    height: 13px;
  }

  .ball-icon {
    width: 18px;
    height: 18px;
  }
}

