.loader-cinemania.is-hidden {
  display: none;
}

#loading-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#loading-text {
  color: #999;
  width: 100px;
  height: 30px;
  text-align: center;
  margin: -7px 0 0 -45px;
  font-family: PT Sans Narrow, sans-serif;
  font-size: 20px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
}

#loading-content {
  width: 170px;
  height: 170px;
  border: 3px solid red;
  margin: -85px 0 0 -85px;
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
}

#loading-content:after {
  content: "";
  border: 3px solid #0f0;
  position: absolute;
  top: 15px;
  bottom: 15px;
  left: 15px;
  right: 15px;
}

#loading-content:before {
  content: "";
  border: 3px solid #00f;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

#loading-content {
  border: 3px solid #4d658d;
  border-color: #4d658d rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: loader 2s linear infinite;
}

#loading-content:before {
  -webkit-animation: loader 3s linear infinite;
  -moz-animation: loader 2s linear infinite;
  -o-animation: loader 2s linear infinite;
  border: 3px solid #d4cc6a;
  border-color: #d4cc6a rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: loader 3s linear infinite;
}

#loading-content:after {
  animation: loader 1.5s linear infinite;
  border: 3px solid #84417c;
  border-color: #84417c rgba(0, 0, 0, 0);
  border-radius: 50%;
  -moz-animation: loader 2s linear infinite;
  -o-animation: loader 2s linear infinite;
}

@-webkit-keyframes loaders {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
